
* {
    box-sizing: border-box; }

$color-blue: #5473B6;
$color-yellow: #F8E31A;

@font-face {
    font-family: 'Rubik Mono One';
    src: local('Rubik Mono One'); // если у пользователя на компе есть, то шрифт загрузится с ПК
    src: url(../fonts/RubikMonoOne-Regular.ttf); }
html {
    scroll-behavior: smooth; }
header {
    background-image: url(../img/bacground4.jpg);
    display: flex;
    justify-content: center; }
.hr_box__mobile {
    display: none; }
hr {
    margin: 0;
    border: none;
    border-top: 8px;
    border-top-style: ridge;
    width: 98%; }
.hr-yellow {
    border-top-color:  $color-yellow;
    animation-name: departure_hr-yellow;
    animation-duration: 2s; // если не указать время анимация не заработает
    animation-delay: 4.2s;// задержка анимации чтобы сайт успел прогрузиться
    animation-timing-function: ease; // чтобы анимация была плавная
    backface-visibility: hidden; //на всякий случай, чтобы анимация не глючила
    animation-fill-mode: backwards; }
@keyframes departure_hr-yellow {
    0% {
        transform: translateX(90rem); }
    70% {
        transform: translateX(-4rem); } }

.hr-blue {
    border-top-color: $color-blue;
    animation-name: departure_hr-blue;
    animation-duration: 2s; // если не указать время анимация не заработает
    animation-delay: 4.2s;// задержка анимации чтобы сайт успел прогрузиться
    animation-timing-function: ease; // чтобы анимация была плавная
    backface-visibility: hidden; //на всякий случай, чтобы анимация не глючила
    animation-fill-mode: backwards; }
@keyframes departure_hr-blue {
    0% {
        transform: translateX(-90rem); }
    70% {
        transform: translateX(4rem); } }

.hr_box {
    display: flex;
    flex-direction: column;
    align-items: center; }


h1 {
    font-size: 5vw;
    color: white;
    padding-top: 2vw;
    padding-bottom: 2vw;
    font-family: 'Rubik Mono One', sans-serif;
    font-weight: 700;
    text-shadow: 10px 10px 30px #5473B6,  -10px -10px 30px #F8E31A;
    -webkit-text-stroke: 0.5px white;
    -webkit-text-fill-color: transparent;
    letter-spacing: 10px;
    text-align: center;
    animation-name: departure_title;
    animation-duration: 2s; // если не указать время анимация не заработает
    animation-delay: 6s;// задержка анимации чтобы сайт успел прогрузиться
    animation-timing-function: ease; // чтобы анимация была плавная
    backface-visibility: hidden; //на всякий случай, чтобы анимация не глючила
    animation-fill-mode: backwards; }
@keyframes departure_title {
    0% {
        opacity: 0; // чтобы плавнопоявлялось
 }        //transform: translateX(-10rem)
    70% {
 }        //transform: translateX(4rem)

    100% {
        opacity: 1; } }


.container {
    max-width: 1440px;
    margin: 0 auto; }


.main-section {
    position: relative;
    min-height: 730px;
    background-image: url(../img/bacground4.jpg);
    background-position: center;
    background-size: cover;
    overflow: hidden;
    &__nav {
        display: flex;
        justify-content: end;
        padding-top: 3vw;
        padding-bottom: 3vw; }
    &__content {
        display: flex;
        max-width: 1440px;
        justify-content: space-around;
 }        //overflow: hidden

    &__right-wraper {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        //justify-content: center
        align-items: center; } }

.left-wraper {
    display: flex;
    flex-wrap: wrap;
    align-content: flex-end; }
.main-img {
    height: 40vw; }
.nav-menu {
    &__button {
        margin-right: 3vw;
        &:last-child {
            margin-right: 5vw; } } }

.right-wraper {
    &__icon-box {
        margin-bottom: 60px; }
    &__button-box {
        display: inline-flex;
        justify-content: center; }
    &__logo-box {
        position: relative;
        width: 200px;
        margin-bottom: 250px;
        margin-left: 15px;
        z-index: 5; } }

.icon-box {
    display: inline-flex; }
.logo-box__item {
    position: absolute; }

.blu_circle {
    width: 200px;
    top: 1px;
    left: 1px;
    animation-name: fli_blue;
    animation-duration: 1.5s;
    animation-iteration-count: 1;
    animation-timing-function: linear;
    animation-delay: 2.5s;
    animation-fill-mode: backwards;
    @keyframes fli_blue {
        0% {
            transform: rotate(560deg);
            top: -600px;
            left: -700px; }
        100% {
            transform: rotate(360deg);
            top: 1px;
            left: 1px; } } }

.grap_pancr {
    width: 165px;
    top: 15px;
    left: 15px;
    animation-name: fli_grap_pancr;
    animation-duration: 0.2s;
    animation-iteration-count: 1;
    animation-timing-function: linear;
    animation-delay: 2s;
    animation-fill-mode: backwards;
    @keyframes fli_grap_pancr {
        0% {
            top: -400px; }
        100% {
            transform: rotate(0deg);
            top: 15px;
            left: 15px; } } }

.lutador {
    width: 80px;
    top: 160px;
    left: 65px;
    animation-name: fli_lutador;
    animation-duration: .6s;
    animation-iteration-count: 1;
    animation-timing-function: linear;
    animation-delay: 2s;
    animation-fill-mode: backwards;
    @keyframes fli_lutador {
        0% {
            top: 1000px; }
        100% {
            top: 160px;
            left: 65px; } } }

.yellow_circle {
    width: 110px;
    top: 40px;
    left: 40px;
    animation-name: fli_yellow;
    animation-duration: 3s;
    animation-iteration-count: 1;
    animation-timing-function: linear;
    animation-delay: 2.5s;
    animation-fill-mode: backwards;
    @keyframes fli_yellow {
        0% {
            transform: rotate(-560deg);
            top: 600px;
            left:  700px; }
        50% {
            transform: rotate(-360deg);
            top: 40px;
            left: 40px; }

        100% {
            transform: rotate(0deg); } } }

.wreslers {
    width: 70px;
    top: 60px;
    left: 60px;
    animation-name: fli_wreslers;
    animation-duration: .6s;
    animation-iteration-count: 1;
    animation-timing-function: linear;
    animation-delay: 2.5s;
    animation-fill-mode: backwards;
    @keyframes fli_wreslers {
        0% {
            top: 60px;
            left: 400px; }
        50% {
            top: 60px;
            left: 400px; }

        100% {
            top: 60px;
            left: 60px; } } }

.button {
    position: relative;
    display: inline-block;
    overflow: hidden;
    &:before {
        content: '';
        position: absolute;
        top: 0px;
        left: 0px;
        width: 1.5em;
        height: 100%;
        background-color: rgba(#fff, 0.6);
        transform: translateX(-2em) skewX(-45deg); }
    &:hover {
        &:before {
            animation: ligh-touch 1s; } } }

@keyframes ligh-touch {
    from {
        transform: translateX(-2em) skewX(-45deg); }
    to {
        transform: translateX(15em) skewX(-45deg); } }

.right-wraper__button {
    &:before {
        content: '';
        position: absolute;
        top: 0px;
        left: 0px;
        width: 1.5em;
        height: 100%;
        background-color: rgba(#fff, 0.6);
        transform: translateX(-2em) skewX(-45deg);
        animation-name: ligh;
        animation-duration: 5s; // общее время анимации
        animation-iteration-count: infinite;
        overflow: hidden; } }


@keyframes ligh {
    0% {
        transform: translateX(-2em) skewX(-45deg); }
    20% {
        transform: translateX(15em) skewX(-45deg); } // задаю интервалы между анимацией 20% от общего времени
    100% {
        transform: translateX(15em) skewX(-45deg); } }
.button {
    padding: 0px;
    background-color: black;
    width: 13em;
    height: 3em;
    border-radius: 100px;
    border-color: $color-yellow;
    overflow: hidden; }

.btn {
    color: $color-yellow;
    font-size: 16px;
    padding: 3em 0;
    text-decoration: none;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-family: sans-serif; //без засечек
    letter-spacing: 3px; // расстояние между буквами
    &_main {
        padding: 3em; } }


.icon-box__item {
    position: relative;
    margin-right: 15px;
    &:last-child {
        margin: 0; } }
i {
    font-size: 35px;
    color: #F8E31A;
    border: none;
    position: relative; }

i:hover {
    border-radius: 100px;
    border-color: $color-yellow;
    box-shadow: 0px 0px 30px; }
