.dropdown {
    &__wrapper {
        display: flex;
        cursor: pointer; // чтобы меню на айфоне тоже раскрывалось
        flex-direction: column;
        position: absolute;
        top: 38vw;
        background-color: black;
        font-weight: bold;
        color: $color-yellow;
        z-index: 3;
        &:hover >.dropdown__item { // знак ">", означает  при наведении на родителя отображается только его прямой потомок(на будующее если надо будет добавить подменю)
            display: flex; }
        &:focus-within >.dropdown__item { //свойство focus-within, чтобы при наведении tab открывалось меню, просто focus не сработает
            display: flex; } }
    &__item {
        text-align: start;
        color: $color-yellow;
        text-decoration: none;
        white-space: nowrap;
        display: none; }
    &__link {
        font-size: 20px;
        line-height: 25px;
        width: 100%;
        text-decoration: none;
        color: $color-yellow;
        padding: 1px;
        letter-spacing: 5px; //мeжбуквенное рассояние
        &:hover {
            color: black;
            background: $color-yellow; }
        &:focus { // действия при нажатии tab
            outline: none; // отключаем заводскую подсветку при на жатии tab
            color: black;
            background: $color-yellow; } } }
