        box-shadow: 0px 0px 30px; } }

//вертикальный телефон
@media (max-width: 575px) {
    .container {
        width: 100%; }
    .main-section {
        height: 770px;
        overflow: hidden;
        &__container {
            margin-top: 20px; } }

    .hr_box {
        display: none; }
    .hr_box__mobile {
        display: block; }
    h1 {
        font-size: 10vw;
        color: white;
        padding-top: 2vw;
        padding-bottom: 2vw;
        font-family: 'Rubik Mono One', sans-serif;
        font-weight: 700;
        text-shadow: 3vw 3vw 8vw #5473B6,  -3vw -3vw 8vw #F8E31A;
        -webkit-text-stroke: 0.5px white;
        -webkit-text-fill-color: transparent;
        letter-spacing: 4vw;
        text-align: center; }

    .left-wraper {
        position: absolute;
        left: -212px;
        display: flex;
        flex-wrap: wrap;
        align-content: flex-end; }
    .main-img {
        height: 657px; }
    .right-wraper {
        padding-top: 30px;
        &__icon-box {
            display: none;
            margin-bottom: 20px; }
        &__button-box {
            display: inline-flex;
            justify-content: center;
            margin-left: 150px; }
        &__logo-box {
            position: relative;
            width: 75px;
            margin-bottom: 160px;
            margin-left: 80px;
            z-index: 5n; } }
    .nav-menu {
        position: fixed;
        display: flex;
        flex-wrap: wrap;
        align-items: end;
        top: 0;
        right: -50vw;
        height: 30vh;
        width: 100%;
        z-index: 7;
        transition: 1s all;
        flex-direction: column;
        justify-content: space-evenly;
        margin-right: 0;
        &__button {
            margin-right: 0;
            &:last-child {
                margin-right: 0; } } }
    .hamburger {
        width: 45px;
        position: fixed;
        top: 8px;
        left: 85%;
        height: 14px;
        z-index: 11;
        &__span {
            height: 2px;
            width: 100%;
            display: block;
            background: $color-yellow;
            margin-bottom: 4px;
            transition: 1s all;
            &:last-child {
                margin-bottom: 0; } } }
    .hamburger_active {
        .hamburger__span {
            margin-bottom: -2px;
            &:nth-last-child(1) {
                transform: translateY(3px) rotate(-45deg); }
            &:nth-last-child(2) {
                display: none; }
            &:nth-last-child(3) {
                transform: translateY(3px) rotate(45deg); } } }
    .nav-menu_active {
        right: 0; }
    .dropdown {
        &__link {
            font-size: 18px;
            line-height: 25px;
            letter-spacing: 2px; } } }









